@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap");

body {
	font-family: "IBM Plex Sans", sans-serif;
	font-size: 16pt;
	color: #292929;
}

html,
body {
	height: 100%;
}

.box {
	display: flex;
	flex-flow: column;
	height: 100%;
}

.titleInput {
	text-align: center;
	margin-bottom: 80px;
}

.newArticleTitle::placeholder {
	font-size: 40px;
	color: white;
	text-shadow: 2px 2px 4px #000000;
}

.newArticleSubtitle::placeholder {
	font-size: 18px;
	color: rgb(200, 200, 200) !important;
	text-shadow: 2px 2px 2px #000000;
}

.newArticleTitle,
.newArticleTitle:focus {
	background-color: transparent;
	border: none !important;
	outline: none !important;
	position: relative;
	top: 10px;
	padding-top: 115px;
	padding-left: 27.5vw;
	padding-right: 27.5vw;
	color: white !important;
	height: 170px !important;
	width: 100%;
	margin-top: 130px;
	height: 140px;
	font-family: "Libre Baskerville", serif !important;
	font-size: 40px !important;
	font-weight: 100 !important;
	text-shadow: 2px 2px 4px #000000;
	overflow: hidden;
	resize: none;
}

.newArticleSubtitle,
.newArticleSubtitle:focus {
	background-color: transparent;
	border: none !important;
	outline: none !important;
	margin-top: 3px;
	margin-bottom: 0px;
	padding-left: 27.5vw;
	padding-right: 27.5vw;
	width: 100%;
	max-height: 70px;
	text-shadow: 2px 2px 2px #000000;
	color: rgb(200, 200, 200) !important;
	font-family: "Libre Baskerville", serif !important;
	font-size: 18px !important;
	font-weight: 100 !important;
	overflow: hidden;
	resize: none;
}

.newCover {
	position: absolute;
	padding-left: 25vw;
	padding-right: 25vw;
	z-index: -1;
	height: 350px !important;
	width: 100%;
	object-fit: cover;
	object-position: 0% 50%;
}

.newCoverBackground {
	z-index: -2;
	position: absolute;
	top: 45px;
	height: 350px !important;
	object-fit: cover;
	object-position: 0% 50%;
	min-width: 95.2%;
	/* Add the blur effect */
	filter: blur(10px);

	transform: scale(1.1);
}

.cutoff {
	position: absolute;
	top: 408.5px;
	height: 40px;
	width: 100%;
	background-color: white;
}

.submitButton {
	border-top-style: solid;
	border-right-style: solid;
	border-left-style: solid;
	border-bottom-style: solid;
	border-top-right-radius: 4.8px;
	border-bottom-right-radius: 4.8px;
	border-top-left-radius: 4.8px;
	border-bottom-left-radius: 4.8px;
	border-width: 1.28px;
	height: 32px !important;
	width: 10%;
	border-color: #c34f6b !important;
	color: #c34f6b !important;
	background-color: white;
}

.submitButton:hover {
	border-color: #c34f6b !important;
	color: white !important;
	background-color: #c34f6b !important;
}

.submitButton:focus {
	outline: none !important;
}

.submitText {
	margin-top: 3px;
	font-size: 15px;
}

.d-flex.justify-content-end.submit {
	margin-top: 3em;
	margin-bottom: 5em;
	margin-right: 25.5vw;
}

.saveButton {
	border-top-style: solid;
	border-right-style: solid;
	border-left-style: solid;
	border-bottom-style: solid;
	border-top-right-radius: 4.8px;
	border-bottom-right-radius: 4.8px;
	border-top-left-radius: 4.8px;
	border-bottom-left-radius: 4.8px;
	border-width: 1.28px;
	height: 32px !important;
	width: 10%;
	border-color: #21b134 !important;
	color: #21b134 !important;
	background-color: white;
	margin-right: 1em;
}

.saveButton:hover {
	border-color: #21b134 !important;
	color: white !important;
	background-color: #21b134 !important;
}

.saveButton:focus {
	outline: none !important;
}

.saveText {
	margin-top: 3px;
	font-size: 15px;
}

.uploadButton {
	position: absolute;
	top: 60px;
	right: 26vw;
	font-size: 1.25em !important;
	color: white !important;
	text-shadow: 2px 2px 4px #464646;
}

.uploadButton:hover {
	cursor: pointer;
}

.content.d-flex.justify-content-center {
	margin-top: 4%;
}

.newArticleLoading {
	position: absolute;
	top: 58px;
	z-index: 100;
	height: 70em !important;
	width: 100% !important;
	padding-top: 350px;
	padding-left: 550px;
	padding-right: 550px;
	margin-bottom: 350px;
	background-color: rgba(200, 200, 200, 0.5);
}
